@import '../../../styles/utils/vars';
@import '../../../styles/utils/mixins';
@import '../../../styles/utils/container';
@import '../../../styles/heading-styles';

.product-details {
  padding-block: calc(24px + $nav-height-mobile) 56px;

  @include on-tablet {
    padding-bottom: 64px;
  }

  @include on-desktop {
    padding-block: calc(24px + $nav-height-desktop) 81px;
  }

  &__product {
    margin-block: 32px 56px;

    &--id {
      display: none;
      color: $nav-link-color;
      text-align: right;
    }

    @include on-tablet {
      @include page-grid;
      margin-block: 40px 64px;

      &--photoBlock {
        grid-column: 1 / 8;
      }

      &--paramsBlock {
        grid-column: 8 / -1;
      }
    }

    @include on-desktop {
      margin-bottom: 80px;

      &--photoBlock {
        grid-column: 1 / 13;
      }

      &--paramsBlock {
        grid-column: 14 / 21;
      }

      &--id {
        display: block;
        grid-column: 21 / -1;
      }
    }
  }

  &__info {
    &--tech {
      margin-top: 56px;

      @include on-tablet {
        margin-top: 64px;
      }

      @include on-desktop {
        margin: 0;
      }
    }

    @include on-desktop {
      @include page-grid;

      &--about {
        grid-column: 1 / 13;
      }

      &--tech {
        grid-column: 14 / -1;
      }
    }
  }

  &__slider {
    margin-block: 56px;

    @include on-tablet {
      margin-block: 64px;
    }

    @include on-desktop {
      margin-block: 80px;
    }
  }
}

.photoBlock {
  display: grid;
  gap: 16px;

  @include on-tablet {
    display: flex;
  }

  &__small-photos {
    display: flex;
    justify-content: center;
    gap: 8px;
    grid-row: 2;

    @include on-tablet {
      flex-direction: column;
      justify-content: flex-start;
      grid-row: 1;
    }

    &--photo {
      object-fit: contain;
      box-sizing: border-box;
      border: 1px solid $shadow-color;
      border-radius: 4px;
      width: 40px;
      height: 40px;
      padding: 5px;

      @include on-tablet {
        width: 35px;
        height: 35px;
      }

      @include on-desktop {
        width: 80px;
        height: 80px;
      }
    }

    &--photo-active {
      border-color: $text-color;
    }
  }

  &__big-photo {
    display: flex;
    justify-content: center;
    width: 100%;

    &--photo {
      object-fit: contain;
      height: 288px;

      @include on-desktop {
        height: 464px;
      }
    }
  }
}

.paramsBlock {
  &__title,
  &__id {
    color: $nav-link-color;
  }

  @include on-desktop {
    &__id {
      display: none;
    }
  }

  &__colorsBlock {
    margin-top: 40px;
    padding-bottom: 24px;
    box-sizing: border-box;
    border-bottom: 1px solid $shadow-color;

    @include on-tablet {
      margin-top: 0;
    }

    &--titleBlock {
      display: flex;
      justify-content: space-between;
      margin-bottom: 8px;
    }

    &--colors {
      display: flex;
      gap: 9px;
    }

    &--colors-container,
    &--colors-color {
      border-radius: 50%;
      box-sizing: border-box;
    }

    &--colors-container {
      height: 32px;
      width: 32px;
      border: 1px solid $shadow-color;
    }

    &--colors-container-active {
      border-color: $text-color;
    }

    &--colors-color {
      height: 30px;
      width: 30px;
      border: 2px solid $bg-button-color
    }
  }

  &__capacityBlock {
    margin-top: 24px;
    padding-bottom: 24px;
    box-sizing: border-box;
    border-bottom: 1px solid $shadow-color;

    &--capacity {
      display: flex;
      gap: 8px;
      margin-top: 8px;
    }

    &--capacity-value {
      display: grid;
      place-items: center;
      text-decoration: none;
      color: $text-color;
      width: 56px;
      height: 32px;
      border: 1px solid $select-border-color;
      border-radius: 4px;
    }

    &--capacity-value-active {
      background-color: $text-color;
      color: $bg-button-color;
    }
  }

  &__priceBlock {
    margin-top: 32px;

    &--prices {
      display: flex;
      gap: 8px;
    }

    &--prices-discount {
      font-size: 32px;
    }

    &--prices-regular {
      display: flex;
      align-items: center;
      font-family: Mont-Regular, sans-serif;
      font-size: 22px;
      line-height: 28.12px;
      color: $nav-link-color;
      text-decoration: line-through;
    }

    &--cart-favourite-Block {
      margin-top: 16px;
      gap: 8px;
      display: flex;
      justify-content: space-between;
    }

    &--cart-favourite-Block-cart {
      width: 100%;
      height: 48px;
      border-radius: 8px;
      background-color: $button-color;
      border: none;
      font-family: Mont-Regular, sans-serif;
      font-size: 14px;
      color: $bg-button-color;
      cursor: pointer;
    }

    &--cart-favourite-Block-favourite {
      min-width: 48px;
      min-height: 48px;
    }
  }

  &__detailsBlock {
    margin-top: 32px;

    &--text {
      font-size: 12px;
    }
  }
}

.about {
  &__title {
    padding-bottom: 16px;
    box-sizing: border-box;
    border-bottom: 1px solid $shadow-color;
  }

  &__text-Block {
    margin-top: 32px;

    &--text {
      margin-top: 16px;
      color: $nav-link-color;
    }
  }
}

.tech {
  &__title {
    padding-bottom: 16px;
    box-sizing: border-box;
    border-bottom: 1px solid $shadow-color;
    margin-bottom: 22px;

    @include on-tablet {
      margin-bottom: 15px;
    }
  }

  &__textBlock {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;

    &--left {
      color: $nav-link-color;
    }
  }
}
